body {
  margin: 0;
}

/* #region stars */
.stars {
  display: flex;
}

.stars__star {
  width: 16px;
  height: 16px;
  margin-right: 4px;
  background-image: url(images/star.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.stars .stars__star:last-child {
  margin-right: 0;
}

.stars--1.stars .stars__star:first-child,
.stars--2.stars .stars__star:nth-child(-n+2),
.stars--3.stars .stars__star:nth-child(-n+3),
.stars--4.stars .stars__star:nth-child(-n+4),
.stars--5.stars .stars__star:nth-child(-n+5) {
  background-image: url(images/star-active.svg);
}
/* #endregion */
